<template>
  <div class="course-cloud-disk">
    <head-top title="云盘">
      <span v-show="activeIndex === 1 && typeValue" slot="left" class="head-back head-first" @click.stop.prevent="toBack">
        <i class="iconfont icon-fanhui"></i>
      </span>
      <span v-show="fileList.length>0" slot="right">
        <van-icon v-if="!editMode" class-prefix="iconfont icon-duoxuananniu" class="head-right"
                  @click="changeMode">
        </van-icon>
        <span v-else>
           <van-icon v-show="fileChecked.length>0" name="delete" class="head-right" @click="toAllDrop"></van-icon>
           <van-icon class-prefix="iconfont icon-fanhui1" class="head-right" @click="editMode = false">
           </van-icon>
        </span>
      </span>
    </head-top>

    <div v-show="activeIndex === 1 && !typeValue" class="content">
      <van-cell-group class="cell_group" v-for="(item, index) in typeList" :key="index">
        <van-cell class="cell_main" :title="item.name" is-link @click="typeClick(item)">
        </van-cell>
      </van-cell-group>
    </div>

    <div v-show="activeIndex === 0 || typeValue" class="content">

      <div v-show="!editMode" class="search_input">
        <van-search placeholder="请输入搜索关键词" v-model="keyValue" @search="searchData"/>
      </div>
      <van-pull-refresh v-show="!editMode" v-model="isLoading" :head-height="80" @refresh="searchData"
                        class="pull_fresh">
        <van-cell-group v-if="fileList && fileList.length>0">
          <van-cell class="cell_main" v-for="(item, index) in fileList" :key="index" @click="fileClick(item)">
            <template slot="icon">
              <van-icon class="cell_icon" class-prefix="iconfont icon-kejian"></van-icon>
            </template>
            <template slot="title">
              <div class="line_title">{{item.fileName + '.' + item.fileExt}}</div>
            </template>
            <template slot="label">
              <div class="line_title">
              <span v-if="item.fileSize > 1024 * 1024">
                {{Math.round(item.fileSize / 1024.0 / 10.24 ) / 100}} MB
              </span>
                <span v-else>
                {{Math.round(item.fileSize / 10.24 ) / 100}} KB
              </span>
              </div>
            </template>
          </van-cell>
        </van-cell-group>
        <r-lack-data v-else></r-lack-data>
      </van-pull-refresh>

      <div v-show="editMode">
        <van-checkbox-group v-model="fileChecked">
          <van-cell-group>
            <van-cell class="cell_main" v-for="(item, index) in fileList" :key="index" clickable
                      @click="fileCheck(item)">
              <template #icon>
                <van-icon class="cell_icon" class-prefix="iconfont icon-kejian"></van-icon>
              </template>
              <template #title>
                <div class="line_title"> {{item.fileName + '.' + item.fileExt}}</div>
              </template>
              <template #label>
                <div class="line_title">
                  <span v-if="item.fileSize > 1024 * 1024">
                    {{Math.round(item.fileSize / 1024.0 / 10.24 ) / 100}} MB
                  </span>
                  <span v-else>
                    {{Math.round(item.fileSize / 10.24 ) / 100}} KB
                  </span>
                </div>
              </template>
              <van-checkbox slot="right-icon" sharp="square" :name="item.id"></van-checkbox>
            </van-cell>
          </van-cell-group>
        </van-checkbox-group>
      </div>
    </div>

    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>

    <van-popup v-model="fileOperate" position="bottom">
      <van-cell
        v-show="fileSelected && (canOpenFileExt.indexOf(fileSelected.fileExt) > -1 || fileSelected.fileTransformed === 2)"
        value="预览" @click.stop.prevent="toPreView">
        <van-icon slot="right-icon" class-prefix="iconfont icon-chakan_yulan"></van-icon>
      </van-cell>
      <van-cell value="删除" @click.stop.prevent="toDrop">
        <van-icon slot="right-icon" class-prefix="iconfont icon-shanchu"></van-icon>
      </van-cell>
    </van-popup>

  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "cloudDisk",
  data () {
    return {
      activeIndex: 0,
      menus: [
        { name: "全部文件", index: 0, icon: "icon-quanbuwenjian" },
        { name: "分类文件", index: 1, icon: "icon-fenlei" }
      ],
      typeList: [],

      keyValue: "",
      typeValue: "",
      fileList: [],
      isLoading: false,

      fileSelected: null,
      fileOperate: false,
      canOpenFileExt: ["txt", "bmp", "gif", "jpg", "png", "ico", "mp4"],

      fileChecked: [],
      editMode: false
    };
  },
  mounted () {
    this.getTypeList();
    this.handleClick(this.menus[0]);
  },
  methods: {
    getTypeList () {
      this.typeList = [];
      this.$mainHttp.materialType.typeList().then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.typeList.push({
                id: item["MTypeID"],
                isSystem: item["MTIsSystem"],
                code: item["MTCode"],
                name: item["MTypename"],
                remark: item["MTDescription"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      })
    },
    handleClick (item) {
      // console.log(item);
      this.fileList = [];
      this.typeValue = "";
      this.keyValue = "";
      if (item.index === 0) {
        this.searchData();
      }
    },
    typeClick (item) {
      this.typeValue = item.id;
      this.searchData();
    },
    searchData () {
      this.fileList = [];
      this.isLoading = true;
      this.$mainHttp.material.dataList({
        key: this.keyValue,
        mtid: this.typeValue,
        uid: this.$store.getters.userID,
        sfield: "",
        sdir: ""
      }).then(res => {
        if (res.Result) {
          if (res.Data && res.Data.length > 0) {
            res.Data.forEach(item => {
              this.fileList.push({
                id: item["MaterialID"],
                directoryID: item["DirectoryID"],
                fileName: item["MaterialName"],
                fileExt: item["MaterialFileExt"],
                fileSize: item["MaterialFileSize"] || 0,
                fileTransformed: item["MaterialLowStreamEstate"],
                createTime: formatDate(item["MaterialCreateDate"], "yyyy-MM-dd")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },

    changeMode () {
      this.fileChecked = [];
      this.editMode = true;
    },
    toBack () {
      this.typeValue = "";
    },
    fileCheck (item) {
      const index = this.fileChecked.findIndex(r => r === item.id);
      if (index > -1) {
        this.fileChecked.splice(index, 1);
      } else {
        this.fileChecked.push(item.id);
      }
    },
    toAllDrop () {
      if (this.fileCheck.length === 0) {
        this.$toast("请选择需要删除的文件");
      } else {
        Dialog.confirm({
          message: "确认删除所有选中的文件？"
        }).then(() => {
          this.toALLDelete();
        }).catch(() => {
        });
      }
    },
    toALLDelete () {
      this.$mainHttp.dirs.dataDelete({
        ids: this.fileChecked.join(","),
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.editMode = false;
          this.searchData();
          this.$toast("批量删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("批量删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },

    fileClick (item) {
      this.fileOperate = true;
      this.fileSelected = item;
    },
    toDrop () {
      Dialog.confirm({
        message: "确认删除当前课时？"
      }).then(() => {
        this.toDelete();
      }).catch(() => {
      });
    },
    toDelete () {
      this.$mainHttp.dirs.dataDelete({
        ids: this.fileSelected.id,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.searchData();
          this.fileOperate = false;
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    toPreView () {
      this.$store.commit("SET_CLOUD_FILE", this.fileSelected);

      if (this.$store.getters.course) {
        this.$gatewayHttp.broadcast.openSource({
          materialID: this.fileSelected.id,
          materialName: this.fileSelected.fileName,
          materialFileExt: this.fileSelected.fileExt
        });
      }

      this.$router.push("/cloud-disk/file-view");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";

.course-cloud-disk {
  min-height: 100%;
  background-color: #fff;

  .head-first{
    z-index: 9;
  }

  .content {
    margin-bottom: 50px;
  }

  .pull_fresh {
    height: 100%;
  }
}
</style>
